}
.sidebar .nav-title {
  font-size: 11px;
  padding: 25px 15px 0;
  letter-spacing: 1px;
}
.sidebar > footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}
/* Menu links */
.main-navigation ul > li > a {
  display: block;
  padding: 10px 15px;
  text-align: left;
  width: 100%;
}
.main-navigation ul > li i {
  padding-right: 10px;
  min-width: 25px;
  text-align: center;
  font-size: 11px;
}
.main-navigation .nav > li > a:hover,
.main-navigation .nav > li > a:focus,
.main-navigation .nav .open > a,
.main-navigation .nav .open > a:hover,
.main-navigation .nav .open > a:focus {
  border-color: transparent;
  background-color: transparent;
}
.main-navigation {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
/* Disable background highlighting on touch devices */
.touch li:hover,
.touch li:focus,
.touch a:hover,
.touch a:focus {
  background-color: transparent!important;
}
/* collapsible menu */
.app .sidebar ul > li {
  position: relative;
  display: block;
}
.app .sidebar ul > li:before,
.app .sidebar ul > li:after {
  content: " ";
  display: table;
}
.app .sidebar ul > li:after {
  clear: both;
}
.app .sidebar ul > li a {
  position: relative;
}
.app .sidebar ul > li.open > .dropdown-menu {
  display: block;
}
.app .sidebar ul > li.open > a .toggle-accordion:before {
  content: "\e64b";
}
.app .sidebar ul > li > ul {
  position: relative;
  display: none;
  padding: 0;
  margin: 0;
  list-style: none;
  -webkit-transition: background-color 400ms ease;
  -moz-transition: background-color 400ms ease;
  -o-transition: background-color 400ms ease;
  transition: background-color 400ms ease;
}
.app .sidebar .nav > li .sub-menu {
