  }
  .app.move-right > .header,
  .app.move-left > .header {
    z-index: 0;
  }
  .layout,
  .header {
    -webkit-backface-visibility: hidden;
    position: relative;
    width: 100%;
    -webkit-transition: transform 300ms ease 0s;
    -moz-transition: transform 300ms ease 0s;
    -o-transition: transform 300ms ease 0s;
    transition: transform 300ms ease 0s;
  }
  .layout:before,
  .layout:after {
    content: " ";
    display: table;
  }
  .layout:after {
    clear: both;
  }
  .offscreen-left {
    -webkit-backface-visibility: hidden;
    width: 280px;
    height: 100%;
    overflow: auto;
    box-sizing: content-box;
    -webkit-overflow-scrolling: touch;
    -webkit-transform: translate(-100%, 0);
    -moz-transform: translate(-100%, 0);
    -ms-transform: translate(-100%, 0);
    -o-transform: translate(-100%, 0);
    transform: translate(-100%, 0);
    -webkit-transform: translate3d(-100%, 0, 0);
    -moz-transform: translate3d(-100%, 0, 0);
    -ms-transform: translate(-100%, 0, 0);
    -o-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
  }
  .offscreen-left * {
    -webkit-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
  }
  .offscreen-right {
    -webkit-backface-visibility: hidden;
    width: 280px;
    height: 100%;
    overflow: auto;
    box-sizing: content-box;
    -webkit-overflow-scrolling: touch;
    -webkit-transform: translate(100%, 0);
    -moz-transform: translate(100%, 0);
    -ms-transform: translate(100%, 0);
    -o-transform: translate(100%, 0);
    transform: translate(100%, 0);
    -webkit-transform: translate3d(100%, 0, 0);
    -moz-transform: translate3d(100%, 0, 0);
    -ms-transform: translate(100%, 0, 0);
    -o-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
  }
  .offscreen-right * {
    -webkit-backface-visibility: hidden;
  }
  .move-left > .layout,
  .move-left > .header {
    -webkit-transform: translate(280px, 0);
    -moz-transform: translate(280px, 0);
    -ms-transform: translate(280px, 0);
    -o-transform: translate(280px, 0);
    transform: translate(280px, 0);
    -webkit-transform: translate3d(280px, 0, 0);
    -moz-transform: translate3d(280px, 0, 0);
    -ms-transform: translate(280px, 0, 0);
    -o-transform: translate3d(280px, 0, 0);
    transform: translate3d(280px, 0, 0);
    -webkit-transition: transform 300ms ease;
    -moz-transition: transform 300ms ease;
    -o-transition: transform 300ms ease;
    transition: transform 300ms ease;
  }
  .move-right > .layout,
  .move-right > .header {
    -webkit-transform: translate(-280px, 0);
    -moz-transform: translate(-280px, 0);
    -ms-transform: translate(-280px, 0);
    -o-transform: translate(-280px, 0);
    transform: translate(-280px, 0);
    -webkit-transform: translate3d(-280px, 0, 0);
    -moz-transform: translate3d(-280px, 0, 0);
    -ms-transform: translate(-280px, 0, 0);
    -o-transform: translate3d(-280px, 0, 0);
    transform: translate3d(-280px, 0, 0);
    -webkit-transition: transform 300ms ease;
    -moz-transition: transform 300ms ease;
    -o-transition: transform 300ms ease;
    transition: transform 300ms ease;
  }
  .offscreen > .layout {
    position: absolute;
    top: 0;
    bottom: 0;
    overflow: visible;
  }
  .no-csstransforms .offscreen-left {
    left: -280px;
  }
  .no-csstransforms .offscreen-right {
    right: -280px;
  }
  .no-csstransforms .move-right > .layout,
  .no-csstransforms .move-right > .header {
    right: 280px;
  }
  .no-csstransforms .move-left > .layout,
  .no-csstransforms .move-left > .header {
    left: 280px;
  }
  .move-left .exit-offscreen,
  .move-right .exit-offscreen {
    -webkit-backface-visibility: hidden;
    cursor: pointer;
    display: block;
    position: absolute;
    background: rgba(255, 255, 255, 0.1);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
