/*
 *  $Chat Box
 *
 */
.chat-box .panel {
  margin-bottom: 5px;
}
.chat-box .chatbox-user {
  margin-bottom: 35px;
}
.chat-box .chatbox-user .chat-time {
  display: block;
  text-align: right;
}
.chat-box .chatbox-user.right .chat-avatar img {
  float: right;
}
.chat-box .chat-avatar {
  width: 48px;
  display: block;
}
.chat-box .message {
  position: relative;
  margin-left: 60px;
}
.chat-box .message:before {
  content: "";
  position: absolute;
  left: 0;
  border-right: 6px solid #fff;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  top: 10px;
  margin-left: -6px;
}
.chat-box .panel-body {
  display: table;
  width: 100%;
}
.chat-box .chatbox-user.right .message {
  position: relative;
  margin-left: 0;
  margin-right: 60px;
}
.chat-box .chatbox-user.right .message .panel {
  background-color: #15db81;
  color: #fff;
}
.chat-box .chatbox-user.right .message .chat-time {
  text-align: left;
}
.chat-box .chatbox-user.right .message:before {
  left: 100%;
  margin-right: 6px;
  margin-left: 0;
  border-right: 6px solid transparent;
  border-left: 6px solid #15db81;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}
.chat-box p {
  margin: 0;
}
@-webkit-keyframes opacity {
  0% {
    opacity: 1;
    filter: alpha(opacity=100);
  }
  100% {
    opacity: 0.4;
    filter: alpha(opacity=40);
  }
}
@-moz-keyframes opacity {
  0% {
    opacity: 1;
    filter: alpha(opacity=100);
  }
  100% {
    opacity: 0.4;
    filter: alpha(opacity=40);
  }
}
.chat-box .typing {
  display: block;
}
.chat-box .typing:before,
.chat-box .typing:after {
  content: '';
}
.chat-box .typing > div,
.chat-box .typing:before,
.chat-box .typing:after {
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background-color: rgba(0, 0, 0, 0.5);
  display: inline-block;
  margin-right: 5px;
}
.chat-box .typing > div,
.chat-box .typing:before,
.chat-box .typing:after {
  -webkit-animation-name: opacity;
  -webkit-animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-name: opacity;
  -moz-animation-duration: 1s;
  -moz-animation-iteration-count: infinite;
}
.chat-box .typing > div {
  -webkit-animation-delay: 100ms;
  -moz-animation-delay: 100ms;
}
.chat-box .typing:after {
  -webkit-animation-delay: 300ms;
  -moz-animation-delay: 300ms;
}
.mail-list {
